import { useState, useEffect } from "react";
import { createRoot } from "@wordpress/element";
import { __ } from "@wordpress/i18n";
import { flatten, differenceBy, debounce } from "lodash";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import ListSubheader from "@mui/material/ListSubheader";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Chip from "@mui/material/Chip";
import Switch from "@mui/material/Switch";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import Autocomplete from "@mui/material/Autocomplete";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import Grid from "@mui/material/Grid";
import HeatPumpOutlinedIcon from "@mui/icons-material/HeatPumpOutlined";
import Collapse from "@mui/material/Collapse";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import { Dialog, Tooltip } from "@mui/material";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import FormHelperText from "@mui/material/FormHelperText";

import TaskAltIcon from "@mui/icons-material/TaskAlt";
import Icon from "@mui/material/Icon";

import AsyncSelect from "react-select/async";

import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import styled from "@emotion/styled";
import { ThemeProvider } from "@mui/material/styles";

import { BootstrapDialogTitle } from "./components/PizzaComponents";
import PizzaRules from "./components/PizzaRules";
import { formatSelectValue } from "./pizza-utils";
import pizzaTheme from "./theme";

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: 48 * 4.5 + 8,
      width: 250,
    },
  },
};

const bgWrapperColors = {
  1: "antiquewhite",
  2: "aliceblue",
  3: "#ffd7bb",
};
const PizzaWrapper = styled.div`
  padding: 20px;
  background-color: ${(prop) => bgWrapperColors[prop.styleChoosen]};
`;
const ProductBox = styled(Box)`
  display: ${(props) => (props.show ? "block" : "none")};
`;
const FormControlFull = styled(FormControl)`
  display: flex;
  margin-bottom: 10px;
  margin-right: 5px;
  margin-left: 5px;
`;
const ComponentMedia = styled(CardMedia)`
  object-fit: contain;
  padding: 16px;
  margin: 0 auto;
  width: auto;
  &:hover {
    cursor: pointer;
  }
`;

const ChooseStyleItem = styled.div`
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  align-items: center;
  justify-items: center;
  cursor: pointer;
  position: relative;
  &:hover .component-style__overlay {
    display: block;
  }
  &.choosen .component-style__overlay {
    display: block;
  }
`;

const ChooseStyleOverlay = styled.div`
  cursor: pointer;
  position: absolute;
  top: -10px;
  right: -10px;
  display: none;
`;

const ChooseStyleImage = styled.img`
  display: block;
  width: 100%;
  margin: 0 auto;
  max-width: 300px;
`;
const ChooseStyleImageBox = styled.div`
  display: block;
  width: 100%;
  margin: 0 auto;
  max-width: 300px;
  position: relative;
`;

const CardMediaEdit = styled.img`
  object-fit: contain;
  &:hover {
    cursor: pointer;
  }
`;
const CardHeaderComponent = styled(CardHeader)`
  & .MuiCardHeader-title {
    font-size: 16px;
  }
`;
function AdminProductFood() {
  const [pizzaComponents, setPizzaComponents] = useState(EV_IL_DATA.pizza_components || []);
  const [foodComponents, setFoodComponents] = useState(EV_IL_DATA.product_ev_components_data || []);

  const [foodComponentsFull, setFoodComponentsFull] = useState(EV_IL_DATA.product_ev_components || []);
  const [openedConditions, setOpenedConditions] = useState(false);

  const openCondtionsModal = () => {
    setOpenedConditions(true);
  };
  const closeCondtionsModal = () => {
    setOpenedConditions(false);
  };
  const maybeRebuildComponents = (components, savedComponents) => {
    const existingIds = flatten(components.map((group) => group.components)).map((component) => component.id);
    const existingTabIds = components.map((group) => group.id);
    const existingProductIds = EV_IL_DATA.products.map((product) => product.id);
    const newConsists = savedComponents.consists_of.consists
      .filter((component) => existingIds.includes(component.id))
      .map((component) => {
        const componentOrigin = flatten(components.map((group) => group.components)).find((c) => c.id === component.id);
        return {
          ...component,
          name: componentOrigin.name,
          image_ID: componentOrigin.image_ID,
          image: componentOrigin.image,
          description: componentOrigin.description,
        };
      });
    const newToAdd = savedComponents.consists_of.to_add
      .filter((component) => existingIds.includes(component.id))
      .map((component) => {
        const componentOrigin = flatten(components.map((group) => group.components)).find((c) => c.id === component.id);
        return {
          ...component,
          name: componentOrigin.name,
          image_ID: componentOrigin.image_ID,
          image: componentOrigin.image,
          description: componentOrigin.description,
        };
      });
    const newExtraComponents = savedComponents.extra.components
      .filter((component) => existingIds.includes(component.id))
      .map((component) => {
        const componentOrigin = flatten(components.map((group) => group.components)).find((c) => c.id === component.id);
        return {
          ...component,
          name: componentOrigin.name,
          image_ID: componentOrigin.image_ID,
          image: componentOrigin.image,
          description: componentOrigin.description,
        };
      });
    const newTabComponents = savedComponents.extra.tab_components
      .filter((tab) => existingTabIds.includes(tab.id))
      .map((tab) => {
        const newTabComps = tab.components
          .filter((c) => existingIds.includes(c.id))
          .map((component) => {
            const componentOrigin = flatten(components.map((group) => group.components)).find((c) => c.id === component.id);
            return {
              ...component,
              name: componentOrigin.name,
              image_ID: componentOrigin.image_ID,
              image: componentOrigin.image,
              description: componentOrigin.description,
            };
          });
        return { ...tab, components: newTabComps };
      });
    const newBortik = savedComponents.bortik.components
      .filter((component) => existingIds.includes(component.id))
      .map((component) => {
        const componentOrigin = flatten(components.map((group) => group.components)).find((c) => c.id === component.id);
        return {
          ...component,
          name: componentOrigin.name,
          image_ID: componentOrigin.image_ID,
          image: componentOrigin.image,
          description: componentOrigin.description,
        };
      });
    const newLayers = savedComponents.layers.components.filter((component) => existingProductIds.includes(component.id));
    return {
      ...savedComponents,
      consists_of: {
        ...savedComponents.consists_of,
        consists: newConsists,
        to_add: newToAdd,
      },
      extra: {
        ...savedComponents.extra,
        components: newExtraComponents,
        tab_components: newTabComponents,
      },
      layers: { ...savedComponents.layers, components: newLayers },
      bortik: { ...savedComponents.bortik, components: newBortik },
      dodo: typeof savedComponents.dodo !== "undefined" ? savedComponents.dodo : false,
      rules: typeof savedComponents.rules !== "undefined" ? savedComponents.rules : [],
    };
  };
  const [productPizzaFull, setProductPizzaFull] = useState(
    (EV_IL_DATA.product_ev_pizza_full && maybeRebuildComponents(EV_IL_DATA.pizza_components, EV_IL_DATA.product_ev_pizza_full)) || {
      consists_of: {
        enabled: false,
        consists: [],

        to_add: [],
      },
      extra: {
        enabled: false,
        tabs: false,
        tab_components: [],
        components: [],
      },
      layers: {
        enabled: false,
        components: [],
      },
      bortik: {
        enabled: false,
        components: [],
      },
      ev_inc: false,
      dodo: false,
      rules: [],
    },
  );

  const [layerProductsData, setLayerProductsData] = useState(productPizzaFull.layers.components);
  const [layerProductsFetched, setLayerProductsFetched] = useState(false);

  //   console.log(EV_IL_DATA.products);
  const [switchState, setSwitchState] = useState({
    consists_of: EV_IL_DATA.product_ev_pizza_full ? EV_IL_DATA.product_ev_pizza_full.consists_of.enabled : false,
    extra: EV_IL_DATA.product_ev_pizza_full ? EV_IL_DATA.product_ev_pizza_full.extra.enabled : true,
  });

  const getDefaultStyleChoosen = () => {
    if (!EV_IL_DATA.product_ev_pizza_full) {
      return "100";
    }
    if (
      EV_IL_DATA.product_ev_pizza_full.consists_of.enabled &&
      typeof EV_IL_DATA.product_ev_pizza_full.dodo !== "undefined" &&
      EV_IL_DATA.product_ev_pizza_full.dodo
    ) {
      return "3";
    } else if (EV_IL_DATA.product_ev_pizza_full.consists_of.enabled) {
      return "2";
    } else if (EV_IL_DATA.product_ev_pizza_full.extra.enabled) {
      return "1";
    }
    return "100";
  };

  const [styleChoosen, setStyleChoosen] = useState(getDefaultStyleChoosen());

  const handleMetaVisible = (e) => {
    setProductPizzaFull({
      ...productPizzaFull,
      bortik: {
        ...productPizzaFull.bortik,
        enabled: e.target.checked,
      },
    });
  };

  const handleLayerVisible = (e) => {
    setProductPizzaFull({
      ...productPizzaFull,
      layers: {
        ...productPizzaFull.layers,
        enabled: e.target.checked,
      },
    });
  };
  const handleSwitchChange = (e) => {
    setSwitchState({
      consists_of: !switchState.consists_of,
      extra: !switchState.extra,
    });
  };
  useEffect(() => {
    //add products data for selected layer products.
    const formData = new FormData();

    formData.append("action", "ev_pizza_get_layers");

    formData.append("nonce", EV_IL_DATA.nonce);
    formData.append("ids", JSON.stringify(productPizzaFull.layers.components));

    try {
      fetch(EV_IL_DATA.ajax_url, {
        method: "POST",
        body: formData,
      })
        .then((response) => {
          return response.json();
        })
        .then((result) => {
          console.log(result);
          setLayerProductsData(result.data.products);

          setLayerProductsFetched(true);
        });
    } catch (err) {
      console.log(err);
    }

    const allMetas = flatten(pizzaComponents.map((group) => group.components)).filter((comp) => comp.meta);
    const metasToAdd = differenceBy(allMetas, productPizzaFull.bortik.components, "id");
    let bortikMod = [];
    productPizzaFull.bortik.components.forEach((c) => {
      allMetas.forEach((c2) => {
        if (c.id === c2.id) {
          bortikMod.push({
            ...c,
            image_ID: c2.image_ID,
            image: c2.image,
            name: c2.name,
          });
        }
      });
    });

    setTimeout(() => {
      if (metasToAdd.length > 0) {
        setProductPizzaFull({
          ...productPizzaFull,
          bortik: {
            ...productPizzaFull.bortik,
            components: [...productPizzaFull.bortik.components, ...metasToAdd],
          },
        });
      }
      if (productPizzaFull.bortik.components.length) {
        setProductPizzaFull({
          ...productPizzaFull,
          bortik: {
            ...productPizzaFull.bortik,
            components: bortikMod,
          },
        });
      }
    }, 1000);
  }, []);

  useEffect(() => {
    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        enabled: switchState.consists_of,
      },
      extra: { ...productPizzaFull.extra, enabled: switchState.extra },
    });
  }, [switchState]);

  const [expandEdit, setExpandEdit] = useState({});
  const handleExpandEdit = (id) => {
    setExpandEdit({ ...expandEdit, [id]: !expandEdit[id] });
  };
  const [expandEditMeta, setExpandEditMeta] = useState({});
  const handleExpandEditMeta = (id) => {
    setExpandEditMeta({ ...expandEditMeta, [id]: !expandEditMeta[id] });
  };
  const handleChangeFood = (e, type) => {
    if (e.target.value === "") return;
    const selectedValues = typeof e.target.value === "string" ? e.target.value.split(",") : e.target.value;

    const selectedComponents = selectedValues.map((id) => {
      const foundComponent = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
        return component.id === id;
      });
      return foundComponent;
    });

    if (type === "extra") {
      if (productPizzaFull.extra.tabs) {
        const selectedTabComponents = pizzaComponents.map((group) => {
          const newPizza = group.components.filter((component) => selectedValues.indexOf(component.id) !== -1);
          return { ...group, components: newPizza };
        });
        setProductPizzaFull({
          ...productPizzaFull,
          extra: {
            ...productPizzaFull.extra,
            tab_components: selectedTabComponents.filter((group) => group.components.length > 0),
            components: selectedComponents,
          },
        });
      } else {
        setProductPizzaFull({
          ...productPizzaFull,
          extra: { ...productPizzaFull.extra, components: selectedComponents },
        });
      }
    } else if (type === "consist") {
      const selectedComponentsModified = selectedValues.map((id) => {
        const foundComponentAlready = productPizzaFull?.consists_of?.consists.find((component) => {
          return component.id === id;
        });
        if (foundComponentAlready) {
          return foundComponentAlready;
        } else {
          const foundComponent = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
            return component.id === id;
          });
          return {
            ...foundComponent,
            position: "",
            required: false,
            visible: true,
          };
        }
      });
      setProductPizzaFull({
        ...productPizzaFull,
        consists_of: {
          ...productPizzaFull.consists_of,
          consists: selectedComponentsModified,
        },
      });
    } else if (type === "add") {
      setProductPizzaFull({
        ...productPizzaFull,
        consists_of: {
          ...productPizzaFull.consists_of,
          to_add: selectedComponents,
        },
      });
    }
  };
  /////
  const setComponentWeight = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, weight: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentDescription = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, description: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentName = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, name: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentPrice = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, price: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentRequired = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, required: e.target.checked } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentVisible = (e, componentId) => {
    const newPizza = productPizzaFull.consists_of.consists.map((component) => {
      return component.id === componentId ? { ...component, visible: e.target.checked } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      consists_of: {
        ...productPizzaFull.consists_of,
        consists: newPizza,
      },
    });
  };
  const setComponentMetaName = (e, componentId) => {
    const newPizza = productPizzaFull.bortik.components.map((component) => {
      return component.id === componentId ? { ...component, name: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      bortik: {
        ...productPizzaFull.bortik,
        components: newPizza,
      },
    });
  };
  const setComponentMetaPrice = (e, componentId) => {
    const newPizza = productPizzaFull.bortik.components.map((component) => {
      return component.id === componentId ? { ...component, price: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      bortik: {
        ...productPizzaFull.bortik,
        components: newPizza,
      },
    });
  };
  const setComponentMetaWeight = (e, componentId) => {
    const newPizza = productPizzaFull.bortik.components.map((component) => {
      return component.id === componentId ? { ...component, weight: e.target.value } : component;
    });

    setProductPizzaFull({
      ...productPizzaFull,
      bortik: {
        ...productPizzaFull.bortik,
        components: newPizza,
      },
    });
  };
  const setLayerProducts = (e, val) => {
    setProductPizzaFull({
      ...productPizzaFull,
      layers: {
        ...productPizzaFull.layers,
        components: val,
      },
    });
  };
  const handleChangeTabs = (e) => {
    const selectedComponentIds = productPizzaFull.extra.components.map((component) => component.id);
    const selectedComponents = pizzaComponents.map((group) => {
      const newPizza = group.components.filter((component) => selectedComponentIds.indexOf(component.id) !== -1);
      return { ...group, components: newPizza };
    });
    setProductPizzaFull({
      ...productPizzaFull,
      extra: {
        ...productPizzaFull.extra,
        tabs: e.target.checked,
        tab_components: selectedComponents.filter((group) => group.components.length > 0),
      },
    });
  };
  const handleChangeInc = (e) => {
    setProductPizzaFull({
      ...productPizzaFull,
      ev_inc: e.target.checked,
    });
  };

  const promiseOptions = (inputValue, callback) => {
    new Promise((resolve) => {
      const formData = new FormData();

      formData.append("action", "ev_pizza_products");

      formData.append("field", inputValue);
      formData.append("all", true);
      formData.append("nonce", EV_IL_DATA.nonce);

      try {
        fetch(EV_IL_DATA.ajax_url, {
          method: "POST",
          body: formData,
        })
          .then((response) => {
            return response.json();
          })
          .then((result) => {
            //console.log( result );
            const newResult = result.data.fields.map((el) => ({
              label: el.post_title,
              value: el.ID,
            }));
            // console.log( newResult );
            resolve(newResult);
          });
      } catch (err) {
        console.log(err);
      }
    }).then((res) => {
      // console.log( res );
      callback(res);
    });
  };
  const loadPromiseOptions = debounce(promiseOptions, 500);

  const handleChangeAsyncSelect = (val) => {
    console.log(val);

    setLayerProductsData(val);
    setProductPizzaFull({
      ...productPizzaFull,
      layers: {
        ...productPizzaFull.layers,
        components: val.map((v) => parseInt(v.value, 10)),
      },
    });
  };

  const chooseStyle = (e, val) => {
    switch (val) {
      case "1":
        setStyleChoosen("1");
        setProductPizzaFull({
          ...productPizzaFull,
          extra: {
            ...productPizzaFull.extra,
            enabled: true,
          },
          consists_of: {
            ...productPizzaFull.consists_of,
            enabled: false,
          },
          dodo: false,
        });
        break;
      case "2":
        setStyleChoosen("2");
        setProductPizzaFull({
          ...productPizzaFull,
          extra: {
            ...productPizzaFull.extra,
            enabled: false,
          },
          consists_of: {
            ...productPizzaFull.consists_of,
            enabled: true,
          },
          dodo: false,
        });
        break;
      case "3":
        setStyleChoosen("3");
        setProductPizzaFull({
          ...productPizzaFull,
          extra: {
            ...productPizzaFull.extra,
            enabled: false,
          },
          consists_of: {
            ...productPizzaFull.consists_of,
            enabled: true,
          },
          dodo: true,
        });
        break;
    }
  };

  return (
   
      <PizzaWrapper styleChoosen={styleChoosen}>
        <input type="hidden" name="product_ev_components_data" value={JSON.stringify(foodComponents)} />
        <input type="hidden" name="product_ev_components" value={JSON.stringify(foodComponentsFull)} />

        <input type="hidden" name="product_ev_pizza_full" value={JSON.stringify(productPizzaFull)} />

        <Box>
          {parseInt(styleChoosen, 10) > 15 ? (
            <>
              <Typography sx={{ textAlign: "center", mb: 2 }} component="h6" variant="h6">
                Choose style
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={4}>
                  <ChooseStyleItem onClick={(e) => chooseStyle(e, "1")}>
                    <Typography component="span" variant="body1">
                      Style #1
                    </Typography>
                    <ChooseStyleImageBox>
                      <ChooseStyleImage src={EV_IL_DATA.url + "/images/pizza-style1.png"} />
                      <ChooseStyleOverlay className={`component-style__overlay ${styleChoosen === "1" ? "choosen" : ""}`}>
                        <TaskAltIcon color="success" />
                      </ChooseStyleOverlay>
                    </ChooseStyleImageBox>
                  </ChooseStyleItem>
                </Grid>
                <Grid item xs={4}>
                  <ChooseStyleItem onClick={(e) => chooseStyle(e, "2")}>
                    <Typography component="span" variant="body1">
                      Style #2
                    </Typography>
                    <ChooseStyleImageBox>
                      <ChooseStyleImage src={EV_IL_DATA.url + "/images/pizza-style2.png"} />
                      <ChooseStyleOverlay className={`component-style__overlay ${styleChoosen === "2" ? "choosen" : ""}`}>
                        <TaskAltIcon color="success" />
                      </ChooseStyleOverlay>
                    </ChooseStyleImageBox>
                  </ChooseStyleItem>
                </Grid>
                <Grid item xs={4}>
                  <ChooseStyleItem onClick={(e) => chooseStyle(e, "3")}>
                    <Typography component="span" variant="body1">
                      Style #3 (Dodo)
                    </Typography>
                    <ChooseStyleImageBox>
                      <ChooseStyleImage src={EV_IL_DATA.url + "/images/pizza-style3.png"} />
                      <ChooseStyleOverlay className={`component-style__overlay ${styleChoosen === "3" ? "choosen" : ""}`}>
                        <TaskAltIcon color="success" />
                      </ChooseStyleOverlay>
                    </ChooseStyleImageBox>
                  </ChooseStyleItem>
                </Grid>
              </Grid>
            </>
          ) : (
            <Button
              variant="outlined"
              sx={{ borderColor: "transparent", mb: 2 }}
              startIcon={<ArrowBackIcon />}
              onClick={() => setStyleChoosen("100")}
            >
              Back to Styles
            </Button>
          )}
        </Box>
        {/* <Box>
  
        <FormControl>
          <label className="ev_pizza_label" htmlFor="extra">
            {__("Extra components block")}
          </label>
          <Switch id="extra" checked={switchState.extra} onChange={handleSwitchChange} color="warning" />
        </FormControl>
        <FormControl style={{ marginLeft: "50px" }}>
          <label className="ev_pizza_label" htmlFor="consists_of">
            {__("Consists of(default for pizza ingredients)")}
          </label>
          <Switch id="consists_of" checked={switchState.consists_of} onChange={handleSwitchChange} color="warning" />
        </FormControl>
      </Box> */}
        <ProductBox show={styleChoosen === "1"}>
          <Box sx={{ mb: 2 }}>
            <Tooltip title={__("Configure rules")} placement="top">
              <Button variant="outlined" color="success" startIcon={<HeatPumpOutlinedIcon />} onClick={openCondtionsModal}>
                {__("Rules")}
              </Button>
            </Tooltip>
            <Dialog onClose={closeCondtionsModal} open={openedConditions} fullWidth maxWidth="lg" scroll="body">
              <BootstrapDialogTitle onClose={closeCondtionsModal}>{__("Rules")}</BootstrapDialogTitle>
              <DialogContent dividers>
                <PizzaRules instance={productPizzaFull} setInstance={setProductPizzaFull} />
              </DialogContent>
              <DialogActions>
                <Button variant="outlined" color="success" onClick={closeCondtionsModal}>
                  {__("Save")}
                </Button>
              </DialogActions>
            </Dialog>
          </Box>
          <FormControl sx={{ m: 1, width: 400 }}>
            <InputLabel id="food-multiple-chip-label">{__("Components", "pizza-builder-for-woocommerce")}</InputLabel>
            <Select
              labelId="food-multiple-chip-label"
              id="food-multiple-chip"
              multiple
              value={productPizzaFull.extra.components.map((component) => component.id)}
              onChange={(e) => handleChangeFood(e, "extra")}
              input={<OutlinedInput id="select-multiple-chip" label={__("Components", "pizza-builder-for-woocommerce")} />}
              renderValue={(selected) => (
                <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
                  {selected.map((value) => {
                    const foundLabel = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
                      return component.id === value;
                    });

                    return <Chip key={value} label={foundLabel.name} />;
                  })}
                </Box>
              )}
              MenuProps={MenuProps}
            >
              {pizzaComponents.map((group) => {
                return [
                  <ListSubheader color="primary" sx={{ fontSize: "20px", fontWeight: "bold" }} key={`group-${group.id}`}>
                    {group.groupName}
                  </ListSubheader>,
                  group.components.map((component) => {
                    return (
                      <MenuItem
                        key={component.id}
                        value={component.id}
                        //   style={getStyles(name, personName, theme)}
                      >
                        {component.name}
                      </MenuItem>
                    );
                  }),
                ];
              })}
            </Select>
          </FormControl>
          <Box>
            <FormControl>
              <FormControlLabel
                control={<Checkbox checked={productPizzaFull.extra.tabs} onChange={handleChangeTabs} />}
                label={__("With tabs")}
              />
            </FormControl>
          </Box>
        </ProductBox>
        <ProductBox show={styleChoosen === "2" || styleChoosen === "3"}>
          <FormControl sx={{ display: "flex" }}>
            <FormControlLabel
              control={<Checkbox checked={productPizzaFull.ev_inc} onChange={handleChangeInc} name="ev_pizza_price_inc" />}
              label={__("Add component prices to product price")}
            />
          </FormControl>
          <Box sx={{ display: "flex" }}>
            <FormControl sx={{ m: 1, width: 400 }}>
              <InputLabel id="food-consists-chip-label">{__("Base components", "pizza-builder-for-woocommerce")}</InputLabel>
              <Select
                labelId="food-consists-chip-label"
                id="food-consists-chip"
                multiple
                value={productPizzaFull.consists_of.consists.map((component) => component.id)}
                onChange={(e) => handleChangeFood(e, "consist")}
                input={<OutlinedInput id="select-consists-chip" label={__("Base components", "pizza-builder-for-woocommerce")} />}
                renderValue={(selected) => (
                  <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
                    {selected.map((value) => {
                      const foundLabel = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
                        return component.id === value;
                      });

                      return <Chip key={value} label={foundLabel.name} />;
                    })}
                  </Box>
                )}
                MenuProps={MenuProps}
              >
                {pizzaComponents.map((group) => {
                  return [
                    <ListSubheader color="primary" sx={{ fontSize: "20px", fontWeight: "bold" }} key={`group-${group.id}`}>
                      {group.groupName}
                    </ListSubheader>,
                    group.components.map((component) => {
                      return (
                        <MenuItem
                          key={component.id}
                          value={component.id}
                          //   style={getStyles(name, personName, theme)}
                        >
                          {component.name}
                        </MenuItem>
                      );
                    }),
                  ];
                })}
              </Select>
            </FormControl>

            <FormControl sx={{ m: 1, width: 400 }}>
              <InputLabel id="food-add-chip-label">{__("Extra components", "pizza-builder-for-woocommerce")}</InputLabel>
              <Select
                labelId="food-add-chip-label"
                id="food-add-chip"
                multiple
                value={productPizzaFull.consists_of.to_add.map((component) => component.id)}
                onChange={(e) => handleChangeFood(e, "add")}
                input={<OutlinedInput id="select-add-chip" label={__("Extra components", "pizza-builder-for-woocommerce")} />}
                renderValue={(selected) => (
                  <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
                    {selected.map((value) => {
                      const foundLabel = flatten(pizzaComponents.map((group) => group.components)).find((component) => {
                        return component.id === value;
                      });

                      return <Chip key={value} label={foundLabel.name} />;
                    })}
                  </Box>
                )}
                MenuProps={MenuProps}
              >
                {pizzaComponents.map((group) => {
                  return [
                    <ListSubheader color="primary" sx={{ fontSize: "20px", fontWeight: "bold" }} key={`group-${group.id}`}>
                      {group.groupName}
                    </ListSubheader>,
                    group.components.map((component) => {
                      return (
                        <MenuItem
                          key={component.id}
                          value={component.id}
                          //   style={getStyles(name, personName, theme)}
                        >
                          {component.name}
                        </MenuItem>
                      );
                    }),
                  ];
                })}
              </Select>
            </FormControl>
          </Box>
          {productPizzaFull.consists_of.consists.length > 0 && (
            <Typography component="h6" variant="h6">
              {__("Base:")}
            </Typography>
          )}

          <Grid container spacing={2}>
            {productPizzaFull.consists_of.consists.map((component) => (
              <Grid item xs={6} md={4} sm={4} xl={3} key={`component-${component.id}`}>
                <Card>
                  <CardHeaderComponent
                    action={
                      <div>
                        <IconButton aria-label="edit" color="secondary" onClick={() => handleExpandEdit(component.id)}>
                          <EditIcon />
                        </IconButton>
                      </div>
                    }
                    title={<span>{component.name}</span>}
                    subheader={
                      <span>
                        {component.weight !== "" && `${component.weight}/`}
                        {`${component.price} ${EV_IL_DATA.wc_symbol}`}
                      </span>
                    }
                    sx={{ flexWrap: "wrap" }}
                  ></CardHeaderComponent>
                  <Collapse in={expandEdit.hasOwnProperty(component.id) ? expandEdit[component.id] : false} timeout="auto" unmountOnExit>
                    <FormControlFull>
                      <TextField
                        type="text"
                        label={__("Name")}
                        name="component_name"
                        value={component.name}
                        onChange={(e) => setComponentName(e, component.id)}
                      />
                    </FormControlFull>
                    <FormControlFull>
                      <TextField
                        type="text"
                        label={__("Price")}
                        name="component_price"
                        value={component.price}
                        onChange={(e) => setComponentPrice(e, component.id)}
                      />
                    </FormControlFull>

                    <FormControlFull>
                      <TextField
                        label={__("Weight")}
                        name="component_weight"
                        value={component.weight}
                        onChange={(e) => setComponentWeight(e, component.id)}
                      />
                    </FormControlFull>
                    <FormControlFull>
                      <FormControlLabel
                        control={
                          <Checkbox checked={component.required} onChange={(e) => setComponentRequired(e, component.id)} color="warning" />
                        }
                        label={__("Required")}
                      />
                    </FormControlFull>
                    <FormControlFull>
                      <FormControlLabel
                        control={
                          <Checkbox checked={component.visible} onChange={(e) => setComponentVisible(e, component.id)} color="warning" />
                        }
                        label={__("Visible")}
                      />
                    </FormControlFull>
                  </Collapse>
                  {(expandEdit.hasOwnProperty(component.id) ? !expandEdit[component.id] : true) && (
                    <ComponentMedia component="img" height="50" image={component.image} alt="" />
                  )}
                </Card>
              </Grid>
            ))}
          </Grid>
          {styleChoosen === "2" && (
            <>
              <Box sx={{ mt: 2 }}>
                <FormControl>
                  <FormControlLabel
                    control={<Checkbox checked={productPizzaFull.bortik.enabled} onChange={handleMetaVisible} color="primary" />}
                    label={
                      <div>
                        {__("Use meta")}
                        <FormHelperText sx={{ m: 0, p: 0 }}>{__("(Sides)", "pizza-builder-for-woocommerce")}</FormHelperText>
                      </div>
                    }
                  />
                </FormControl>
              </Box>
              {productPizzaFull.bortik.enabled && (
                <Grid container spacing={2}>
                  {productPizzaFull.bortik.components.map((component) => (
                    <Grid item xs={6} md={4} sm={4} xl={3} key={`component-${component.id}`}>
                      <Card>
                        <CardHeaderComponent
                          action={
                            <div>
                              <IconButton aria-label="edit" color="secondary" onClick={() => handleExpandEditMeta(component.id)}>
                                <EditIcon />
                              </IconButton>
                            </div>
                          }
                          title={<span>{component.name}</span>}
                          subheader={
                            <span>
                              {component.weight !== "" && `${component.weight}/`}
                              {`${component.price} ${EV_IL_DATA.wc_symbol}`}
                            </span>
                          }
                          sx={{ flexWrap: "wrap" }}
                        ></CardHeaderComponent>
                        <Collapse
                          in={expandEditMeta.hasOwnProperty(component.id) ? expandEditMeta[component.id] : false}
                          timeout="auto"
                          unmountOnExit
                        >
                          <FormControlFull>
                            <TextField
                              type="text"
                              label={__("Name")}
                              name="component_name"
                              value={component.name}
                              onChange={(e) => setComponentMetaName(e, component.id)}
                            />
                          </FormControlFull>
                          <FormControlFull>
                            <TextField
                              type="text"
                              label={__("Price")}
                              name="component_price"
                              value={component.price}
                              onChange={(e) => setComponentMetaPrice(e, component.id)}
                            />
                          </FormControlFull>

                          <FormControlFull>
                            <TextField
                              label={__("Weight")}
                              name="component_weight"
                              value={component.weight}
                              onChange={(e) => setComponentMetaWeight(e, component.id)}
                            />
                          </FormControlFull>
                        </Collapse>
                        {(expandEditMeta.hasOwnProperty(component.id) ? !expandEditMeta[component.id] : true) && (
                          <ComponentMedia component="img" height="50" image={component.image} alt="" />
                        )}
                      </Card>
                    </Grid>
                  ))}
                </Grid>
              )}
              <div>
                <FormControl>
                  <FormControlLabel
                    control={<Checkbox checked={productPizzaFull.layers.enabled} onChange={handleLayerVisible} color="primary" />}
                    label={
                      <div>
                        {__("Enable layer")}
                        <FormHelperText sx={{ m: 0, p: 0 }}>{__("(Additional products)", "pizza-builder-for-woocommerce")}</FormHelperText>
                      </div>
                    }
                  />
                </FormControl>
              </div>
              {productPizzaFull.layers.enabled && (
                //   <Autocomplete
                //     multiple
                //     id="products-outlined"
                //     options={EV_IL_DATA.products}
                //     getOptionLabel={(option) => option.name}
                //     isOptionEqualToValue={(option, value) => option.id === value.id}
                //     value={productPizzaFull.layers.components}
                //     filterSelectedOptions
                //     onChange={setLayerProducts}
                //     renderInput={(params) => <TextField {...params} label={__("Products")} placeholder={__("Product")} />}
                //   />

                <FormControl sx={{ width: "100%" }} className="pizza-async-select">
                  <AsyncSelect
                    value={formatSelectValue(layerProductsData)}
                    cacheOptions
                    isMulti
                    isClearable
                    loadOptions={loadPromiseOptions}
                    defaultOptions={EV_IL_DATA.products}
                    placeholder={__("Type to search", "pizza-builder")}
                    onChange={(val) => handleChangeAsyncSelect(val)}
                    styles={{
                      control: (baseStyles, state) => ({
                        ...baseStyles,
                        //height: '56px',
                      }),
                      valueContainer: (baseStyles, state) => ({
                        ...baseStyles,
                        zIndex: "10000",
                        paddingBottom: 0,
                        paddingTop: 0,
                      }),
                      menu: (baseStyles, state) => ({
                        ...baseStyles,
                        zIndex: "10000",
                        bottom: "100%",
                        top: "auto",
                      }),
                      indicatorsContainer: (baseStyles, state) => ({
                        ...baseStyles,
                        //height: '56px',
                      }),
                      input: (baseStyles, state) => ({
                        ...baseStyles,
                        margin: "0px",
                        padding: "0px",
                      }),
                    }}
                  />
                </FormControl>
              )}
            </>
          )}
        </ProductBox>
      </PizzaWrapper>
    
  );
}

createRoot(document.getElementById("ilfood_data")).render( <ThemeProvider theme={ pizzaTheme }><AdminProductFood /></ThemeProvider>);
