import {__} from '@wordpress/i18n';
export const getNameActions = (name) => {
	if(isUuidString(name)) {
		name = 'isUuid';
		}
	switch(name) {
		case 'isUuid':
			return [
				{
					label: __('Weight'),
					value: 'weight'
				},
				{
					label: __('Total Price'),
					value: 'total_price'
				},
				{
					label: __('Quantity'),
					value: 'quantity'
				}
			];
			case 'selected_extra':
				return [
					{
						label: __('Weight'),
						value: 'weight'
					},
					{
						label: __('Total Price'),
						value: 'total_price'
					},
				];
			default:
				return [
					{
						label: __('Custom'),
						value: 'custom'	
					}
				];
	}
}
export const getRuleActions = (name) => {
	switch(name) {
		default:
			return [
				{
					label: __('Add fee'),
					value: 'fee'
				},
				{
					label: __('Add discount'),
					value: 'discount'
				},
				{
					label: __('Hide'),
					value: 'hide'
				},
				{
					label: __('Custom action'),
					value: 'custom'
				},
			];
	}
}
export const isUuidString = (str) => {
	// Regular expression to check if string is a valid UUID
const regexExp = /^[0-9a-fA-F]{8}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{12}$/gi;

if(regexExp.test(str)) {
	return true;
}
return false;
}
