export const getAllowedComparator = ( option ) => {
	// const fieldType = getFieldTypeById(fieldName);
	return option === 'user_logged_in' || option === 'product_meta_key'
		? false
		: true;
};
export const getComparators = ( option ) => {
	// console.log( option );
	switch ( option ) {
		case 'product_sku':
		case 'user_code':
		case 'user_city':
			return { '=': '=', '!=': '!=' };
		case 'product_category':
		case 'cart_coupon':
		case 'user_role':
		case 'user_state':
		case 'user_country':
		case 'cart_product':
			return { IN: 'in', 'NOT IN': 'not_in' };

		default:
			return {
				'=': '=',
				'!=': '!=',
				'>': '>',
				'>=': '>=',
				'<=': '<=',
				'<': '<',
			};
	}
};

export const escapedComparator = ( comparator ) => {
	return comparator.replace( '&gt;', '>' ).replace( '&lt;', '<' );
};

export const getCriteriaValue = ( option, type ) => {
	//user
	if ( type === 'user' ) {
		if ( option === 'user_logged_in' ) {
			return {
				type: 'user_logged_in',
				values: [
					{ label: 'false', value: 0 },
					{ label: 'true', value: 1 },
				],
			};
		} else if ( option === 'user_role' ) {
			return {
				type: 'user_role',
				values: Object.entries( WCMASTER_ADMIN.user_roles ).map(
					( [ key, name ] ) => ( {
						label: name,
						value: key,
					} )
				),
			};
		} else if ( option === 'user_country' ) {
			return {
				type: 'user_country',
				values: Object.entries( WCMASTER_ADMIN.countries ).map(
					( [ key, name ] ) => ( {
						label: name,
						value: key,
					} )
				),
			};
		} else if ( option === 'user_state' ) {
			// console.log( WCMASTER_ADMIN.states );
			return {
				type: 'user_state',
				values: Object.entries( WCMASTER_ADMIN.states ).map(
					( [ key, options ] ) => {
						const stateOption = Object.entries( options ).map(
							( [ stateKey, stateName ] ) => ( {
								label: stateName,
								value: stateKey,
							} )
						);
						return { label: key, options: stateOption };
					}
				),
			};
		}
		return {
			type,
			values: '',
		};
	}
	//cart
	if ( type === 'cart' ) {
		switch ( option ) {
			case 'cart_product':
				return { type: option, values: [] };
			case 'cart_coupon':
				const couponOptions = WCMASTER_ADMIN.coupons.map( ( el ) => ( {
					label: el.post_title,
					value: el.ID,
				} ) );
				return { type: option, values: couponOptions };
			case 'cart_quantity':
			case 'cart_subtotal':
			default:
				return { type: option, values: '' };
		}
	}
	if ( type === 'product' ) {
		switch ( option ) {
			case 'product_category':
				const catOptions = WCMASTER_ADMIN.categories.map( ( el ) => ( {
					label: el.label,
					value: el.ID,
				} ) );
				return { type: option, values: catOptions };
			case 'product_meta_key':
				const metaOptions = WCMASTER_ADMIN.meta_keys.map( ( el ) => ( {
					label: el,
					value: el,
				} ) );
				return { type: option, values: metaOptions };
			default:
				return { type: option, values: 0 };
		}
	}
};
export const getOptionType = ( option ) => {
	if (
		getCartOptions()
			.map( ( o ) => o.id )
			.includes( option )
	) {
		return 'cart';
	} else if (
		getUserOptions()
			.map( ( o ) => o.id )
			.includes( option )
	) {
		return 'user';
	} else if (
		getProductOptions()
			.map( ( o ) => o.id )
			.includes( option )
	) {
		return 'product';
	}
};

export const parseNumber = ( str, defaultValue = 0 ) => {
	const num = parseFloat( str );
	if ( isNaN( num ) || ! isFinite( num ) ) {
		return defaultValue;
	}
	return num;
};

export const formatSelectValue = ( data ) => {
	
	if ( ! Array.isArray( data ) ) {
		return [];
	}
	const formattedValues = data
		.map( ( pair ) => {
			if(pair.label) {
				return pair;
			}
			return false;
		} )
		.filter( ( el ) => el );
	// console.log( 'values:', formattedValues );
	return formattedValues;
};
