/**
 * External dependencies
 */
import { useState } from "@wordpress/element";
import { __ } from "@wordpress/i18n";
import { createHooks } from "@wordpress/hooks";
import {
  Panel,
  PanelHeader,
  PanelBody,
  
  ButtonGroup,
  SelectControl,
  TextControl,
  __experimentalUnitControl as UnitControl,
} from "@wordpress/components";

import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import DeleteIcon from "@mui/icons-material/Delete";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import AddIcon from '@mui/icons-material/Add';
import TextField from "@mui/material/TextField";
import Chip from "@mui/material/Chip";
import Autocomplete from "@mui/material/Autocomplete";
/**
 * Internal dependencies
 */
import {
  getShippingMethodOptions,
  getCartOptions,
  getUserOptions,
  getProductOptions,
  getComparators,
  getCriteriaValue,
  getAllowedComparator,
  getOptionType,
  escapedComparator,
  getNameActions,
  getRuleActions,
} from "../pizza-utils";

import { ConditionPanelRow, PanelRowActions, PanelRowContent } from "./PizzaComponents";

import "../sass/admin/pizza.scss";

const PizzaRules = ({ instance, setInstance }) => {
  const [isOpened, setIsOpened] = useState({});
  const shippingMethodoptions = getShippingMethodOptions();
  const cartOptions = getCartOptions();
  const userOptions = getUserOptions();
  const productOptions = getProductOptions();
  const units = [
    {
      value: EV_IL_DATA.wc_symbol,
      label: EV_IL_DATA.wc_symbol,
      default: 0,
    },
    { value: "%", label: "%", default: 0 },
  ];
  console.log(instance);
  const pizzaRules = {};
  pizzaRules.hooks = createHooks();

  /**
   * Empty Condition
   */
  const emptyRule = {
    name: "",
    name_action: "",
    comparator: "",
    name_value: "",
    action: "",
    value: "",
  };
  /**
   * Add a condition
   */
  const addRule = () => {
    const instanceRules = typeof instance.rules !== "undefined" && Array.isArray(instance.rules) ? instance.rules : [];
    setInstance({
      ...instance,

      rules: [...instanceRules, emptyRule],
    });
    // console.log(instance);
  };
  /**
   * Select condition
   * @param option
   * @param i
   */
  const selectRule = (option, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return {
            ...r,
            name: option,
            action: r.action === "" ? "fee" : r.action,
            name_action: r.name_action === "" ? "weight" : r.name_action,
          };
        }
        return r;
      }),
    });
  };

  const setRuleValue = (value, i) => {
    console.log(value);
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return { ...r, value: value };
        }
        return r;
      }),
    });
  };
  const setRuleAction = (val, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          let actionValue = r.value;
          if (["fee", "discount", "custom"].indexOf(val) !== -1 && Array.isArray(r.value)) {
            actionValue = "";
          } else if (["hide"].indexOf(val) !== -1 && typeof r.value === "string") {
            actionValue = [];
          }

          return { ...r, action: val, value: actionValue };
        }
        return r;
      }),
    });
  };
  const setRuleNameAction = (val, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return { ...r, name_action: val };
        }
        return r;
      }),
    });
  };
  const setRuleNameValue = (val, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return { ...r, name_value: val };
        }
        return r;
      }),
    });
  };
  /**
   * Select comparator
   * @param conditionIndex
   * @param statementIndex
   */
  const selectComparator = (val, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return { ...r, comparator: val };
        }
        return r;
      }),
    });
  };
  /**
   * Select If
   */
  const selectIf = (i, type) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((c, index) => {
        if (index === i) {
          return { ...c, if: type };
        }
        return c;
      }),
    });
  };
  /**
   *
   * @param val
   * @param i
   */
  const handleChangeConditionValue = (val, i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((c, index) => {
        if (index === i) {
          return { ...c, value: val };
        }
        return c;
      }),
    });
  };
  /**
   *
   * @param i Condition index
   */
  const addCriteria = (i) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((c, index) => {
        if (index === i) {
          return {
            ...c,
            statements: [
              ...c.statements,
              {
                criteria: "",
                comparator: "",
                value: "",
                type: "",
              },
            ],
          };
        }
        return c;
      }),
    });
  };
  /**
   *
   * @param conditionIndex Condition index
   */
  const duplicateCondition = (conditionIndex) => {
    const foundCondition = instance.rules.find((c, index) => conditionIndex === index);
    const newRules = [...instance.rules];

    newRules.splice(conditionIndex, 0, foundCondition);

    setInstance({
      ...instance,

      rules: newRules,
    });
  };
  /**
   *
   * @param i Condition index
   */
  const deleteCondition = (i) => {
    const newRules = instance.rules.filter((c, index) => index !== i);
    setInstance({
      ...instance,

      rules: newRules,
    });
  };
  const openCondition = (i) => {
    setIsOpened({
      ...isOpened,
      [i]: typeof isOpened[i] !== "undefined" ? !isOpened[i] : false,
    });
  };
  /**
   * Select statement
   * @param option
   * @param conditionIndex
   * @param i
   */
  const selectStatement = (option, conditionIndex, i) => {
    const conditionField = instance.rules.find((cond, index) => index === conditionIndex);
    const optionType = getOptionType(option);

    const newStatementData = conditionField.statements.map((st, index) => {
      if (i === index) {
        return {
          ...st,
          criteria: option,
          comparator: Object.values(getComparators(option))[0],
          value: "",
          type: optionType,
        };
      }
      return st;
    });
    setInstance({
      ...instance,

      rules: instance.rules.map((cond, index) => {
        if (index === conditionIndex) {
          return { ...cond, statements: newStatementData };
        }
        return cond;
      }),
    });
  };

  /**
   *
   * @param conditionIndex
   * @param statementIndex
   */
  const cloneStatement = (conditionIndex, statementIndex) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((c, index) => {
        if (conditionIndex === index) {
          const foundStatement = c.statements.find((st, i) => statementIndex === i);

          const newStatements = [...c.statements];
          newStatements.splice(statementIndex, 0, foundStatement);

          return { ...c, statements: newStatements };
        }
        return c;
      }),
    });
  };
  /**
   *
   * @param conditionIndex
   * @param statementIndex
   */
  const deleteStatement = (conditionIndex, statementIndex) => {
    setInstance({
      ...instance,

      rules: instance.rules.map((c, index) => {
        if (conditionIndex === index) {
          const newStatements = c.statements.filter((st, i) => statementIndex !== i);

          return { ...c, statements: newStatements };
        }
        return c;
      }),
    });
  };

  const renderRuleValue = (rule, index) => {
    switch (rule.action) {
      case "fee":
      case "discount":
        return <UnitControl onChange={(value) => setRuleValue(value, index)} value={rule.value} units={units} />;
      case "hide":
        const selectValue = instance.extra.components.map((c) => ({ label: c.name, id: c.id }));
        console.log(selectValue);
        return (
          <Autocomplete
            sx={{ width: "300px" }}
            multiple
            options={selectValue}
            getOptionLabel={(option) => option.label}
            isOptionEqualToValue={(option, value) => {
              return option.id === value.id;
            }}
            onChange={(e, val) => handleHideActionSelect(val, index)}
            defaultValue={Array.isArray(instance.rules[index].value) ? instance.rules[index].value : []}
            filterSelectedOptions
            renderInput={(params) => <TextField {...params} label={__("Components")} />}
          />
        );
      default:
        return <TextControl value={rule.value} onChange={(val) => setRuleValue(val, index)} />;
    }
  };
  const handleHideActionSelect = (val, i) => {
    console.log(val);
    setInstance({
      ...instance,

      rules: instance.rules.map((r, index) => {
        if (index === i) {
          return { ...r, value: val };
        }
        return r;
      }),
    });
  };
  return (
    <div>
      <div className="wcmaster-rules-header">
        <Button sx={{mb:2}} variant="outlined" color="success" onClick={() => addRule()} startIcon={<AddIcon />}>
          {__("Add", "pizza-builder-for-woocommerce")}
        </Button>
      </div>
      {instance.rules?.map((c, i) => (
        <Panel key={i} className="wcpizza-rules-panel">
          <PanelHeader>
            <PanelRowContent>
              <SelectControl value={c.name} onChange={(val) => selectRule(val, i)} className="">
                <option value="">{__("Select group", "pizza-builder-for-woocommerce")}</option>
                <optgroup label={__("Extra components", "pizza-builder-for-woocommerce")}>
                  {instance.extra.components.map((s, index) => (
                    <option value={s.id} key={index}>
                      {s.name}
                    </option>
                  ))}
                </optgroup>
                <optgroup label={__("Other", "pizza-builder-for-woocommerce")}>
                  <option value="selected_extra" disabled>{__("Selected extra", "pizza-builder-for-woocommerce")}</option>
                  <option value="selected_included" disabled>{__("Selected base", "pizza-builder-for-woocommerce")}</option>
                </optgroup>
              </SelectControl>
              {c.name && (
                <SelectControl value={c.name_action} onChange={(val) => setRuleNameAction(val, i)}>
                  {getNameActions(c.name).map((option, index) => (
                    <option key={option + "-" + index} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </SelectControl>
              )}

              {c.name && getAllowedComparator(c.name_action) && (
                <SelectControl
                  value={escapedComparator(c.comparator)}
                  onChange={(val) => selectComparator(val, i)}
                  className="select-comparator"
                >
                  <option value="">{__("Select comparator", "pizza-builder-for-woocommerce")}</option>
                  {Object.keys(getComparators(c.name_action)).map((comp, index) => (
                    <option value={getComparators(c.name_action)[comp]} key={"comp-" + index}>
                      {comp}
                    </option>
                  ))}
                </SelectControl>
              )}
              {c.name && <TextControl value={c.name_value} onChange={(val) => setRuleNameValue(val, i)} />}
              {c.name && (
                <SelectControl value={c.action} onChange={(val) => setRuleAction(val, i)}>
                  {getRuleActions(c.name).map((option, index) => (
                    <option key={option + "-" + index} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </SelectControl>
              )}
              {c.name && renderRuleValue(c, i)}
            </PanelRowContent>
            <PanelRowActions>
              <IconButton color="error" className="wccon-button-remove" onClick={() => duplicateCondition(i)} sx={{ ml: "auto" }}>
                <ContentCopyIcon />
              </IconButton>
              <IconButton color="error" onClick={() => deleteCondition(i)} sx={{ ml: "auto" }}>
                <DeleteIcon />
              </IconButton>
            </PanelRowActions>
          </PanelHeader>
        </Panel>
      ))}
    </div>
  );
};

export default PizzaRules;
