/**
 * External dependencies
 */
import styled from "@emotion/styled";

import CloseIcon from '@mui/icons-material/Close';
import DialogTitle from '@mui/material/DialogTitle';

import IconButton from "@mui/material/IconButton";
import CardMedia from "@mui/material/CardMedia";
import FormControl from "@mui/material/FormControl";
import MuiAlert from '@mui/material/Alert';


export const ComponentMedia = styled(CardMedia)`
  object-fit: contain;

  &:hover {
    cursor: pointer;
  }
`;
export const FormControlFull = styled(FormControl)`
  display: flex;
  margin-bottom: 10px;
  margin-right: 5px;
  margin-left: 5px;
`;
export const FormControlImage = styled(FormControl)`
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  margin-left: 20px;
  column-gap: 10px;
`;
export const FormControllSettings = styled.div`
  display: flex;
  align-items: center;
`;
export const AccordionHeaderInner = styled.div`
  display: flex;
  align-items: center;
`;
export const CardMediaEdit = styled.img`
  object-fit: contain;
  &:hover {
    cursor: pointer;
  }
`;
export const IconButtonAdd = styled(IconButton)`
  background-color: rgb(25, 118, 210);
  :hover {
    background-color: rgb(21, 101, 192);
  }
  color: white;
  margin: 30px;
`;

export const ConditionPanelRow = styled.div`
display:flex;
`;
export const PanelRowActions = styled.div`
display:flex;
`;
export const PanelRowContent = styled.div`
display:flex;
flex-wrap: wrap;
align-items: center;
column-gap: 10px;
& .components-base-control__field {
	margin-bottom: 0;
}
`;

export const BootstrapDialogTitle = ( props ) => {
	const { children, onClose, ...other } = props;

	return (
		<DialogTitle sx={ { m: 0, p: 2 } } { ...other }>
			{ children }
			{ onClose ? (
				<IconButton
					aria-label="close"
					onClick={ onClose }
					sx={ {
						position: 'absolute',
						right: 8,
						top: 8,
						color: ( theme ) => theme.palette.grey[ 500 ],
					} }
				>
					<CloseIcon />
				</IconButton>
			) : null }
		</DialogTitle>
	);
};


export const Alert = ( props ) => {
	return <MuiAlert elevation={ 6 } severity={ props.type } variant="filled" { ...props } />;
};